<template>
<div>
    <div class=" w70p pt48 pb48" style="margin:0 auto;">
        <div class="firstTitle flexBetween">碳市日历
            <!-- <span class="f18 cloc">{{nowDay}}</span> -->
        </div>
        <div>
            <calendar2 :model-value="value" hide-type @on-change="changeTime" @on-prev="prev" @on-next="next">
                <template #month="{ date, data }">
                    <div>
                        <!-- <Badge status="warning" text="看苹果发布会" v-if="data.day === '2023-02-10'" />
                        <Badge status="success" text="中秋节" v-if="data.day === '2023-02-15'" />
                        <Badge status="error" text="国庆节" v-if="data.day === '2023-02-19'" /> -->
                        <span v-for="(v,o) in list" :key="o" @click="showDetail(v)" >
                            <Badge status="success" :text="v.title" v-if="data.day === v.date.substr(0,10)" />
                        </span>
                    </div>
                </template>
            </calendar2>
        </div>
    </div>
    <!--弹窗-->
    <newsPop v-model="isPop" :info="info"></newsPop>
</div>
</template>

<script>
import dayjs from 'dayjs'
import calendar2 from '@/components/calendar/index.js'
import newsPop from '@/components/newsPop'
export default {
    name: '',
    props: {
    },
    components: {
        calendar2,newsPop
    },
    data() {
        return {
            topItems: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
            // value: '2023-02-10',
            value:this.$dayjs(new Date()).format('YYYY-MM-DD'),
            list:[],
            info:{},
            isPop:false,
        }
    },
    mounted() {
        this.getList(this.value);
    },
    methods: {
        showDetail(v){
            console.log(v)
            this.info=v;
            this.isPop=true;
        },
        async getList(val){
            const date = new Date(val)
            const start = date.setDate(1); //本月第一天
            const end = new Date(date.getFullYear(), date.getMonth() + 1, 1)//本月的最后一天。下一个月
            // console.log(start,end,89)
            // console.log(this.$dayjs(start).format('YYYY-MM-DD'),this.$dayjs(end).format('YYYY-MM-DD'),89)
            let d={
                // begin:new Date('2022-01-01 00:00:00').getTime(),
                // end:new Date().getTime(),
                begin:new Date(this.$dayjs(start).format('YYYY-MM-DD')).getTime(),
                end:new Date(this.$dayjs(end).format('YYYY-MM-DD')).getTime(),
            };
            let res = await this.$api.event_list(d);
            this.list=res || [];
            // console.log(res,'res')
        },
        changeTime(val){
            // console.log(this.$dayjs(val).format('YYYY-MM-DD'),'日期')
            this.getList(val);
        },
        prev(){
            // this.getList();
        },
        next(){
            // this.getList();
        }
    },
    computed: {
        nowDay() {
            return dayjs(new Date()).format('YYYY-MM-DD');
        }
    }
}
</script>

<style scoped lang='less'>
//左右
:deep(.ivu-btn) {
    background: #444757;
    color: hsla(0, 0%, 100%, .9);
    border-color: #444757;
}

:deep(.ivu-calendar-header) {
    padding: 12px 12px;
}

:deep(.ivu-calendar-header-title) {
    color: #fff;
}

:deep(.ivu-calendar-table td) {
    border-bottom: 1px solid #53576a;
    border-right: 1px solid #53576a;
}

:deep(.ivu-calendar-table tr:first-child td) {
    border-left: 1px solid #53576a;
}

:deep(.ivu-calendar-table tr td:first-child) {
    border-left: 1px solid #53576a;
}

:deep(.ivu-calendar-table tr:first-child td) {
    border-top: 1px solid #53576a;
}

:deep(.ivu-calendar-table-day-current) {
    background-color: #333;
}

:deep(.ivu-calendar-table-day:hover) {
    background-color: #222;
}

:deep(.ivu-calendar-table-day-title) {
    color: #fff;
    opacity: .7;
}

:deep(.ivu-calendar-table-day-current .ivu-calendar-table-day-title) {
    color: #fff;
    opacity: 1;
}

:deep(.ivu-badge-status-text) {
    color: #ffffff;
}



:deep(.ivu-calendar-header) {
    border: none;
}
:deep(.ivu-modal-header){
    background: #212222;
}
</style>